<template>
    <div class="timeout" >
        <div class='search_header' v-if='enterRouter == "/search"'>
            <img src="../../assets/image/search_big.png" alt="" class='search_icon'>
            <div class='input'>
                <!-- <form action="javascript:void 0"> -->
                <input autofocus='autofocus' type="search" v-model="content" @keyup.13="tapToSearch" class='inputContent'>
                <!-- </form> -->
                <!-- <input type="text" class='input' v-model="content"> -->
                <div class='delete' @click='deleteWord()'>
                    <img src="../../assets/image/删除.png" alt="">
                </div>
            </div>
            <div class='cancel' @click='cancel()'>取消</div>
        </div>
        <div class="header"
            v-if='enterRouter != "/item_bank"
                    && enterRouter != "/data_list"
                    && enterRouter != "/set"
                    && enterRouter != "/login"
                    && enterRouter != "/text_main"
                    && enterRouter != "/text_main_mes"
                    && enterRouter != "/search"'>
            <div class="back"  @click.stop="back()">
                <img src="../../assets/image/联合42.png" alt="">
            </div>
            <div class="book_list_head">{{title}}</div>
        </div>
        <div class='timeout_container'>
            <img src="../../assets/image/重新连接.png" alt="">
            <div class='word'>
                您已和地球失去连接
                <div @click='retry()' class='retryButton'>点击重试</div>
            </div>
        </div>
        <div class='footer' v-if='enterRouter == "/item_bank" || enterRouter == "/data_list" || enterRouter == "/set"'>
            <div v-for='(item,index) in navArr'
                :key="index"
                :class='{home_nav:true , current:isShow == index}'>
                <span class='normal' @click='go(index)'><img :src="item.img" alt=""></span>
                <span class='clickImg'><img :src="item.clickImg" alt=""></span>
                <p>{{item.title}}</p>
            </div>
        </div>
    </div>
</template>
<script>
import index from '../../assets/image/首页图标.png'
import indexClick from '../../assets/image/首页点击.png'
import study from '../../assets/image/学习图标.png'
import studyClick from '../../assets/image/学习点击.png'
import mine from '../../assets/image/我的图标.png'
import mineClick from '../../assets/image/我的点击.png'
export default {
  name: 'timeout',
  data () {
    return {
      title: '',
      enterRouter: '',
      isShow: 0, // 被选中
      navArr: [
        { title: '新闻', path: '/data_list', img: study, clickImg: studyClick },
        { title: '配网规范', path: '/item_bank', img: index, clickImg: indexClick },
        // {title:'考试',path:'/text_list_wait',img:examination,clickImg:examinationClick},
        { title: '我的', path: '/set', img: mine, clickImg: mineClick }
      ],
      routerFrom: [
        { routerPath: '/book_list', title: '文档学习' },
        { routerPath: '/change_pwd', title: '修改密码' },
        { routerPath: '/jf_detail', title: '积分明细' },
        { routerPath: '/jf_explain', title: '积分说明' },
        { routerPath: '/jf_ranking', title: '排行榜' },
        { routerPath: '/look_text_item', title: '回顾试卷' },
        { routerPath: '/me_like', title: '我喜欢' },
        { routerPath: '/news_list', title: '新闻' },
        { routerPath: '/recently_look', title: '最近浏览' },
        { routerPath: '/study_rank_list', title: '学习排行榜' },
        { routerPath: '/text_list_start', title: '在线考试' },
        { routerPath: '/text_person', title: '个人信息' },
        { routerPath: '/video_study', title: '视听学习' },
        { routerPath: '/exercisePage', title: '题库练习' }

      ],
      content: '' // 输入框内容
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => { //  这里的vm指的就是vue实例，可以用来当做this使用
      vm.enterRouter = from.path
      for (var i = 0; i < vm.navArr.length; i++) {
        if (vm.enterRouter === vm.navArr[i].path) {
          vm.isShow = i
        }
      }
      for (var j = 0; j < vm.routerFrom.length; j++) {
        if (vm.enterRouter === vm.routerFrom[i].routerPath) {
          vm.title = vm.routerFrom[i].title
        }
      }
    })
  },
  created () {
    if (localStorage.getItem('content')) {
      this.content = localStorage.getItem('content')
    } else {
      this.content = ''
    }
  },
  mounted () {
    // 监听返回键
    document.addEventListener('backbutton', this.onBackKeyDown, false)
  },
  // 消除返回键监听
  beforeDestroy () {
    document.removeEventListener('backbutton', this.onBackKeyDown, false)
  },
  methods: {
    onBackKeyDown () {
      if (this.enterRouter === '/item_bank' ||
                this.enterRouter === '/data_list' ||
                this.enterRouter === '/set' ||
                this.enterRouter === '/login') {
        navigator.Backbutton.goHome()
      } else if (this.enterRouter === '/search') {
        this.cancel()
      } else {
        this.back()
      }
    },
    // 取消按钮
    cancel () {
      localStorage.removeItem('content')
      this.back()
    },
    go (index) {
      this.isShow = index
      this.$router.replace(this.navArr[index].path) // 跳转
    },
    /** 返回 */
    back () {
      if (this.enterRouter === '/look_text_item' ||
            this.enterRouter === '/text_grade') {
        this.$router.replace({
          name: 'text_list_wait',
          params: { fromText: 1 }
        })
      } else {
        this.$router.back()
      }
    },
    /** 重新连接 */
    retry () {
      if (this.enterRouter === 'text_main') {
        this.$router.back()
      } else {
        this.$router.replace(this.enterRouter)
      }
    }
  },
  watch: {
    content (val, oldval) {
      localStorage.setItem('content', val)
      this.$router.replace(this.enterRouter)
    }
  }
}
</script>
<style scoped>
    .timeout{
        width:100vw;
        height:100vh;
    }
    .header{
        width:100%;
        background:#fff;
        font-size:0.38rem;
        font-family: PingFang-SC-Medium;
        position: fixed;
        top:0;
        height: 0.94rem;
        z-index: 99;
        display: flex;
        justify-content: center;
    }
    .back{
        width:0.89rem;
        height:0.94rem;
        position: fixed;
        top:0;
        left:0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 100;
    }
    .back img{
        width:0.19rem;
        height:0.34rem;
    }
    .book_list_head{
        line-height: 0.94rem;
        font-size:0.38rem;
        color:#4E4E4E;
        width:3rem;
        overflow-x:hidden;
        text-overflow:ellipsis;
        padding-top:0.02rem;
        white-space:nowrap;
        text-align: center
    }
    .timeout_container{
        width:100%;
        /* height:calc(100vh - 0.94rem); */
        display: flex;
        flex-direction: column;
        margin-top:2.76rem;
        align-items: center;
    }
    .timeout_container img{
        width:2.91rem;
        height:2.63rem;
        display: block;
    }
    .timeout_container .word{
        font-size: 0.32rem;
        color:rgba(159, 167, 175, 1);
        margin-top:0.4rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        width:100%;
        height:1.12rem;
        justify-content: space-between;
    }
    .timeout_container .retryButton{
        color:rgba(45, 126, 255, 1);
    }
    .footer{
    display: flex;
    position: fixed;
    bottom: 0;
    z-index:50;
    width: calc(100vw);
    border-top: 0.01rem solid rgba(242,242,242,0.9);
    background-color: #fff;
    height:0.98rem;
    padding:0.01rem 0 0.07rem 0;
  }
  .home_nav{
    flex:1;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: space-between;
    font-size: 0.22rem;
  }
  .home_nav span{
    /* flex:1; */
    font-size:0.25rem;
    /* height:0.36rem; */
    display: flex;
    justify-content: center
  }
  .home_nav span img{
    width:0.64rem;
    height:0.64rem;
    display: block;
  }
  .home_nav p{
    font-size: 0.22rem;
    /* height: 0.2rem; */
    color:#9A9A9A;
  }
  .home_nav .clickImg{
    display: none;
  }
  .current{
    color: #5794F6;
  }
  .current .normal{
    display: none;
  }
  .current .clickImg{
    display:flex;
  }
  .current p{
    color: #5794F6;
  }
  .search_header{
        width:100%;
        background:#fff;
        height:0.76rem;
        display: flex;
        align-items: center;
        border-bottom:0.012rem solid #EBEFF5;
        position: fixed;
        top:0;
        left:0;
        z-index: 2;
    }
    .search_header .search_icon{
        width:0.34rem;
        height:0.35rem;
        margin:0 0.29rem 0 0.31rem;
    }
    .search_header .input{
        height:0.74rem;
        position: relative;
        display: flex;
        align-items: center;
        width:calc(100vw - 2.05rem);
        font-size:0.36rem;
        color:#2D2D2F;
    }
    .search_header .input input{
        height:0.73rem;
        width:100%;
        outline: none;
        padding: 0.18rem 0.4rem 0.18rem 0;
    }
    .search_header .input .delete{
        height:0.76rem;
        position: absolute;
        top:0;
        right:0;
        width:0.4rem;
        display: none;
        align-items: center;
        justify-content: flex-end;
    }
    .search_header .input .delete img{
        width:0.4rem;
        height:0.4rem;
    }
    .search_header .cancel{
        width:0.85rem;
        height: 0.76rem;
        line-height: 0.82rem;
        margin-left:0.26rem;
        font-size:0.28rem;
        color:#4890FC;
        display: flex;
        /* align-items: center; */
    }
</style>



